;(function() {
    var mainPageSlider = new Swiper('.main__slider', {
        direction: 'horizontal',
        loop: true,
        speed: 800,
        autoplay: {
            delay: 4000
        },
        breakpoints: {
            1200: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            320: {
                slidesPerView: 'auto',
                loopedSlides: 6,
                spaceBetween: 8,
                loop: true
            }
        },
        navigation: {
            nextEl: '.slider-next',
            prevEl: '.slider-prev',
        },
    });
    const mainPageSliderElement = document.querySelector('.main__slider');
    mainPageSliderElement.addEventListener('mouseover', mainPageSlider.autoplay.stop);
    mainPageSliderElement.addEventListener('mouseleave', mainPageSlider.autoplay.start);

    const burgerBtn = document.querySelector('#burger');
    const headerMenu = document.querySelector('.header__top');

    burgerBtn.addEventListener('click', toggleHeaderMenu);

    function toggleHeaderMenu() {
        if (headerMenu.classList.contains('active')) {
            headerMenu.classList.remove('active');
            burgerBtn.classList.remove('active');
            document.body.classList.remove('lock');
        } else {
            headerMenu.classList.add('active');
            burgerBtn.classList.add('active');
            document.body.classList.add('lock');
        }
    }
    /* Кнопка "подать объявление" и ее появление при скролле */
    if (window.innerWidth <= 767) {
        let scrollPrev = 0;
        const btnAddAdv = document.querySelector('#addAdvBtn');
        window.addEventListener('scroll', function() {
            let scrolled = document.documentElement.scrollTop;
            if (scrolled > 50 && scrolled < scrollPrev) {
                btnAddAdv.classList.add('visible');
            } else {
                btnAddAdv.classList.remove('visible');
            }
            scrollPrev = scrolled;
        });
    }
    /* ===== */
})();